<template>
  <div class="articleCase-container">
    <!-- È¢ÑËßàÊñáÁ´†ÂàÜÁ±ª -->
    <el-card class="box-card">
      <div slot="header" class="clearfix header-box">
        <span>ÊñáÁ´†ÂàÜÁ±ª</span>
        <el-button type="primary" @click="addCateBtnFn">Ê∑ªÂä†ÂàÜÁ±ª</el-button>
      </div>
      <!-- ‰ΩøÁî®element-uiÁöÑtableÁªÑ‰ª∂ -->
      <el-table :data="cateList" style="width: 100%" border stripe>
        <el-table-column label="Â∫èÂè∑" type="index" width="100">
        </el-table-column>
        <el-table-column label="ÂàÜÁ±ªÂêçÁß∞" prop="cate_name"></el-table-column>
        <el-table-column label="ÂàÜÁ±ªÂà´Âêç" prop="cate_alias"></el-table-column>
        <el-table-column align="Êìç‰Ωú">
          <!-- Âà©Áî®ÊèíÊßΩËé∑ÂèñË°åÊï∞ÊçÆ -->
          <!-- scopeÂØπË±°Ôºö{row: Ë°åÂØπË±°} -->
          <template slot-scope="scope">
            <el-button
              size="mini"
              type="primary"
              @click="updateCateBtnFn(scope.row)"
              >‰øÆÊîπ</el-button
            >
            <el-button
              size="mini"
              type="danger"
              @click="deleteCateBtnFn(scope.row)"
              >Âà†Èô§</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </el-card>

    <!-- Ê∑ªÂä†„ÄÅ‰øÆÊîπÊñáÁ´†ÂàÜÁ±ª-ÂØπËØùÊ°Ü -->
    <el-dialog
      title="ÊèêÁ§∫"
      :visible.sync="dialogVisible"
      width="35%"
      @closed="ondialogCloseFn"
    >
      <!-- ÂÜÖÈÉ®Ë°®ÂçïÂå∫Âüü -->
      <el-form
        :model="addForm"
        :rules="addRules"
        ref="addRef"
        label-width="80px"
      >
        <el-form-item label="ÂàÜÁ±ªÂêçÁß∞" prop="cate_name">
          <el-input
            v-model="addForm.cate_name"
            minlength="1"
            maxlength="10"
          ></el-input>
        </el-form-item>
        <el-form-item label="ÂàÜÁ±ªÂà´Âêç" prop="cate_alias">
          <el-input
            v-model="addForm.cate_alias"
            minlength="1"
            maxlength="15"
          ></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancelFn">Âèñ Ê∂à</el-button>
        <el-button type="primary" @click="confirmFn">Á°Æ ÂÆö</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
import {
  getArtCateListAPI,
  addArtCateAPI,
  updateArtCateAPI,
  delArtCateAPI
} from '@/api/index'

export default {
  name: 'articleCase',
  data() {
    return {
      cateList: [],
      dialogVisible: false,
      // Â§Ñ‰∫éÊ∑ªÂä†ËøòÊòØ‰øÆÊîπÁä∂ÊÄÅ ÁöÑ Ê†áËÆ∞
      isEdit: false,
      editId: '',
      // Ê∑ªÂä†Ë°®ÂçïÁöÑÊï∞ÊçÆÂØπË±°
      addForm: {
        cate_name: '',
        cate_alias: ''
      },
      // Ê∑ªÂä†Ë°®ÂçïÁöÑÈ™åËØÅËßÑÂàôÂØπË±°
      addRules: {
        cate_name: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂàÜÁ±ªÂêçÁß∞', trigger: 'blur' },
          {
            pattern: /^\S{1,10}$/,
            message: 'ÂàÜÁ±ªÂêçÂøÖÈ°ªÊòØ1-10‰ΩçÁöÑÈùûÁ©∫Â≠óÁ¨¶',
            trigger: 'blur'
          }
        ],
        cate_alias: [
          { required: true, message: 'ËØ∑ËæìÂÖ•ÂàÜÁ±ªÂà´Âêç', trigger: 'blur' },
          {
            pattern: /^[a-zA-Z0-9]{1,15}$/,
            message: 'ÂàÜÁ±ªÂà´ÂêçÂøÖÈ°ªÊòØ1-15‰ΩçÁöÑÂ≠óÊØçÊï∞Â≠ó',
            trigger: 'blur'
          }
        ]
      }
    }
  },
  created() {
    // Ëé∑ÂèñÊñáÁ´†ÂàÜÁ±ª
    this.initCateListFn()
  },
  methods: {
    // Ëé∑ÂèñÊñáÁ´†ÂàÜÁ±ª
    async initCateListFn() {
      const res = await getArtCateListAPI()
      this.cateList = res.data.data
    },
    // - Ê∑ªÂä†„ÄÅ‰øÆÊîπÊñáÁ´†ÂàÜÁ±ªÂØπËØùÊ°ÜÂèñÊ∂àÊåâÈíÆ
    cancelFn() {
      this.dialogVisible = false
    },
    // - Ê∑ªÂä†„ÄÅ‰øÆÊîπÊñáÁ´†ÂàÜÁ±ªÂØπËØùÊ°ÜÁ°ÆËÆ§ÊåâÈíÆ
    confirmFn() {
      // Ë°®ÂçïÊ†°È™å
      this.$refs.addRef.validate(async (valid) => {
        if (valid) {
          if (this.isEdit) {
            // + ‰øÆÊîπÊìç‰Ωú
            // ÊñπÂºè1
            // this.addForm.id = this.editId
            // const { data: res } = await updateArtCateAPI(this.addForm)

            // ÊñπÂºè2 üéâ(Áõ¥Êé•‰ª•ÁªìÊûÑËµãÂÄºÁöÑÊñπÂºè‰º†ÂÖ•Êï∞ÊçÆ)
            const { data: res } = await updateArtCateAPI({
              id: this.editId,
              ...this.addForm
            })
            // console.log(res)
            // ËØ∑Ê±ÇÊàêÂäü„ÄÅÂ§±Ë¥•ÊèêÁ§∫
            if (res.code !== 0) return this.$message.error(res.message)
            this.$message.success(res.message)
          } else {
            // + Êñ∞Â¢ûÊìç‰Ωú
            const { data: res } = await addArtCateAPI(this.addForm)
            // console.log(res)
            // ËØ∑Ê±ÇÊàêÂäü„ÄÅÂ§±Ë¥•ÊèêÁ§∫
            if (res.code !== 0) return this.$message.error(res.message)
            this.$message.success(res.message)
          }

          // Âà∑Êñ∞ÂàóË°®Êï∞ÊçÆ Âπ∂ ÂÖ≥Èó≠ÂØπËØùÊ°Ü
          this.initCateListFn()
          this.dialogVisible = false
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    // - Ê∑ªÂä†„ÄÅ‰øÆÊîπÊñáÁ´†ÂàÜÁ±ª-ÂÖ≥Èó≠Êó∂ÁöÑÊ∏ÖÁ©∫Êï∞ÊçÆ
    ondialogCloseFn() {
      this.$refs.addRef.resetFields()
    },
    // Ê∑ªÂä†ÊñáÁ´†ÂàÜÁ±ª-Ëß¶ÂèëÊåâÈíÆ
    addCateBtnFn() {
      // Ê†áËÆ∞Áä∂ÊÄÅ
      this.isEdit = false
      this.editId = ''

      this.dialogVisible = true
    },
    // ‰øÆÊîπÊñáÁ´†ÂàÜÁ±ª-Ëß¶ÂèëÊåâÈíÆ
    updateCateBtnFn(obj) {
      console.log(obj)
      // Ê†áËÆ∞Áä∂ÊÄÅ
      this.isEdit = true
      this.editId = obj.id

      // ÂÆûÁé∞Êï∞ÊçÆÂõûÊòæ
      this.dialogVisible = true

      // Ëß£ÂÜ≥ÁÇπÂáª‰øÆÊîπÊåâÈíÆÔºåÂêéÁÇπÂáªÊ∑ªÂä†ÊåâÈíÆÊó∂Êï∞ÊçÆÂõûÊòæÁöÑÂ∞èbug
      this.$nextTick(() => {
        this.addForm.cate_name = obj.cate_name
        this.addForm.cate_alias = obj.cate_alias
      })
    },
    // Âà†Èô§ÊñáÁ´†ÂàÜÁ±ª-Ëß¶ÂèëÊåâÈíÆ
    async deleteCateBtnFn(obj) {
      console.log(obj)
      const { data: res } = await delArtCateAPI(obj.id)

      // ËØ∑Ê±ÇÊàêÂäü„ÄÅÂ§±Ë¥•ÊèêÁ§∫
      if (res.code !== 0) return this.$message.error('Âà†Èô§ÂàÜÁ±ªÂ§±Ë¥•ÔºÅ')
      this.$message.success('Âà†Èô§ÂàÜÁ±ªÊàêÂäüÔºÅ')
      // ÈáçÊñ∞ËØ∑Ê±ÇÂàóË°®Êï∞ÊçÆ
      this.initCateListFn()
    }
  }
}
</script>
<style lang="less" scoped>
.header-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
</style>
